






















































































































































































































import Vue from "vue";

import * as CocoSSD from "../coco-ssd/index";
import { ObjectDetection } from "../coco-ssd/index";

import { Observable, Subject } from "rxjs";
import {
  extractBlurCoefficient,
  extractImageFromPrediction,
  makeblob
} from "../utils/imageUtils";
import { parseValuesFromImagesAndUpdateForm } from "@/api/api";
import { DetectionState, Prediction, RootState } from "./types";

import { defer, from } from "rxjs";
import {
  auditTime,
  bufferCount,
  debounceTime,
  exhaust,
  filter,
  map,
  reduce,
  scan
} from "rxjs/operators";

declare const cv: any;
declare const Webcam: any;

// TODO: move to separate module
const predictionsSubject = new Subject<Array<Prediction>>();

// TODO: move to separate module
const stateReducer = (
  currentState: DetectionState,
  predictions: Array<Prediction>
): DetectionState => {
  const newState: DetectionState = {};

  // if list of predictions is empty -> clear state
  if (predictions.length == 0) {
    return newState;
  }

  const predictionMap: { [key: string]: Prediction } = {};
  const capturedClasses = predictions.map(x => x[0].class);

  predictions.forEach(prediction => {
    const [detectedObject, blob] = prediction;
    predictionMap[detectedObject.class] = prediction;
  });

  const containsClass = (c: string) => capturedClasses.some(x => x.includes(c));

  // DETECT DEVICE TYPE

  if (containsClass("smns_value")) {
    newState.deviceType = "smns";
  } else if (containsClass("daisy")) {
    newState.deviceType = "daisy";
  } else if (containsClass("zpa_type_l")) {
    newState.deviceType = "zpa_l";
  } else if (containsClass("zpa_type_r")) {
    newState.deviceType = "zpa_r";
  }

  // TODO: reset state if device type changes

  // EXTRACT SERIAL NUMBER

  /* eslint-disable */
  if (predictionMap.hasOwnProperty('sn')) {
    newState.sn = {
      score: predictionMap['sn'][0].score,
      image: predictionMap['sn'][1]!
    }
  }

  // EXTRACT VALUE

  /* eslint-disable */
  if (predictionMap.hasOwnProperty('smns_value')) {
    newState.value = {
      score: predictionMap['smns_value'][0].score,
      image: predictionMap['smns_value'][1]!
    }
  }

  /* eslint-disable */
  if (predictionMap.hasOwnProperty('value')) {
    newState.value = {
      score: predictionMap['value'][0].score,
      image: predictionMap['value'][1]!
    }
  }

  // EXTRACT TARIFF
  if (containsClass('_t1') || containsClass('_t2') || containsClass('_p')) {

    const tariffClass = predictions.find(p => 
          p[0].class.includes('_p') || 
          p[0].class.includes('_t1') || 
          p[0].class.includes('_t2'));

    newState.tariff = {
      score: tariffClass![0].score,
      image: tariffClass![1]!,
      class: tariffClass![0].class
    }
  }

  return newState;
}

export default Vue.extend({
  name: "Home",
  data(): RootState {
    return {
      cameraButtonEnabled: true,
      model: undefined,
      predictions: [],
      cameraOptions: [],
      selectedCamera: '',
      blurCoef: 0,
      form: {
        sn: "",
        value_t1: "",
        value_t2: "",
        value_p: "",
        value_smns: ""
      },
      detectedDeviceType: null,
      analysisInProgress: false
    };
  },
  created() {
    // Before we can use COCO-SSD class we must wait for it to finish
    // loading. Machine Learning models can be large and take a moment
    // to get everything needed to run.
    CocoSSD.load().then(loadedModel => {
      this.model = loadedModel;
    });
  },
  mounted() {

    const isStateComplete = (state: DetectionState) => {

      if (state.deviceType == "smns") {
        // for "smns" value is enough
        return !!state.value;
      } else {
        // all other devices require full info (sn/value/tariff)
        return !!state.sn && !!state.value && !!state.tariff && !!state.deviceType
      }
    }

    const mapStateToApiCallAction = (state: DetectionState): Observable<void> => {
      // create asynchronous action, which will parse data through API and update the form state
      const observableAction = defer(() => from(parseValuesFromImagesAndUpdateForm(state, this)));
      return observableAction;
    }

    const o1 = predictionsSubject.pipe(scan(stateReducer, {})); // merge multiple measurements together

    // FIXME: do not work, fix this!
    // o1.pipe(map(ds => ds.deviceType))
    //   .pipe(bufferCount(2, 2))
    //   .pipe(filter(buffer => buffer.length == 2 && buffer[0] != null && buffer[0] != buffer[1]))
    //   .subscribe({
    //     next: (v: any) => {
    //       console.log('device type has changed: ', v);
    //     }
    //   });

    const apiCallsObservable = o1       
        .pipe(filter(isStateComplete))      // filter out incomplete state (do not process it)
        .pipe(map(mapStateToApiCallAction)) // create API call from state
        .pipe(auditTime(2000))              // trottle 2 seconds and take last measurement (for more stable results)
        .pipe(exhaust());                   // block any other API calls until the previous call is finished

    const sb2 = apiCallsObservable.subscribe({
      next: () => {
        /* eslint-disable */
      },
      error: () => {
        console.log('error from API observable');
      },
    });
  },
  watch: {
    /**
     * If another camera is selected from select box -> reinitialize camera stream.
     */
    selectedCamera: function (newDeviceId) {

      const video = this.$refs.webcam as any;
      const canvas = this.$refs.canvas as any;

      console.log(newDeviceId);

      const constraints = {
        video: {
          advanced: [{
            deviceId: newDeviceId
          }]
        }  
      };

      // Activate the webcam stream.
      navigator.mediaDevices
        .getUserMedia(constraints)
        .then(stream => {
          video.setAttribute("autoplay", "");
          video.setAttribute("muted", "");
          video.setAttribute("playsinline", "");
          video.srcObject = stream;

          // initialize canvas
          canvas.setAttribute("width", 640);
          canvas.setAttribute("height", 480);
        })
        .catch(e => console.log(e));
    }
  },
  methods: {
    /**
     * Triggered, when we click on button "Enable Camera"
     */
    enableCam: function(event: any) {

      const video  = this.$refs.webcam as any;
      const canvas = this.$refs.canvas as any;

      if (!navigator.mediaDevices || !navigator.mediaDevices.enumerateDevices) {
        alert("enumerateDevices() not supported.");
        return;
      }

      const constraints = { video: { facingMode: "environment" }, audio: false };
      
      
      navigator.mediaDevices
          .getUserMedia(constraints)
          .then((stream) => {
          video.srcObject = stream;
          this.cameraButtonEnabled = false;
      })
      .catch(function(error) {
          console.error("Oops. Camera does not work", error);
      });

      // navigator.mediaDevices.enumerateDevices().then((devices) => {

      //   alert(JSON.stringify(devices));
      //   const filteredDevices = devices
      //   //.filter(d => d.kind == "videoinput");
      
      //   const cameraOptions = filteredDevices.map(d => ({value: d.deviceId, text: d.label}))
      //   this.cameraOptions = cameraOptions;

      //   if (cameraOptions.length != 0) {
      //     this.selectedCamera = cameraOptions[0].value; 
      //   }

      //   // hide camera button
      //   this.cameraButtonEnabled = false;
      // });
    },
    predictWebcam() {
      
      const sourceImage = this.takepicture();

      const canvas = this.$refs.canvas as any;
      const current_sn_canvas = this.$refs.current_sn as any;
      const current_value_canvas = this.$refs.current_value as any;
      const current_tariff_canvas = this.$refs.current_tariff as any;

      const blurCoef = extractBlurCoefficient(canvas);

      // render blur coefficient to screen
      this.blurCoef = blurCoef;

      // constants (used to configure classification process)
      const maxNumBoxes = 20;
      const minScore = 0.5;
      const blurThreshold = 16;
      const probabilityThreshold = 0.9;

      // Now let's start classifying a frame in the stream.
      this.model!.detect(canvas, maxNumBoxes, minScore).then(predictions => {

        const isOverThreshold = (prob: any) => prob.score > probabilityThreshold;

        // only run analysis when image is clean,
        // predictions contain higher probabilities
        // and no other analysis (API call) is running
        if (predictions.length != 0 && 
           predictions.every(isOverThreshold) && 
           blurCoef > blurThreshold &&
           !this.analysisInProgress) {

          // analyze and convert predictions (i.e. extract image data for each prediction)
          const extractedImages: Array<Prediction> = predictions.map(
            (prediction, index) => {
              const extractedImage = extractImageFromPrediction(
                prediction,
                sourceImage,
                prediction.class == 'value' // scale image 0.5x if it's value (to avoid memory overflows)
              );

              // check the type of predicted image and select different canvas based on the value
              let target_canvas = null
              if (prediction.class == 'value' || prediction.class == 'smns_value') {
                target_canvas = current_value_canvas;
              } else if (prediction.class == 'sn') {
                target_canvas = current_sn_canvas;
              } else if (prediction.class.includes('_t1') ||
                         prediction.class.includes('_t2') ||
                         prediction.class.includes('_p')) {
                target_canvas = current_tariff_canvas;
              }

              // if known class is found -> render images to canvas and then convert it to blob
              // and send to RxJS streaming pipeline
              if (target_canvas !== null) {

                // render extracted image to canvas element
                cv.imshow(target_canvas, extractedImage);

                // get base64 data from rendered image
                const base64image = target_canvas
                  .toDataURL("image/png")
                  .replace("data:image/png;base64,", "");

                  var image = document.createElement('image') as any;
                  image.src = 'data:image/bmp;base64,'+ base64image;
                  document.body.appendChild(image);

                // convert base64 string data to blob (binary array)
                const imageBlob = makeblob(base64image, "image/png");
                return [prediction, imageBlob];
              } else {
                return [prediction, null];
              }

              extractedImage.delete();
            }
          );

          // send predictions along with extracted images to RxJs pipeline
          predictionsSubject.next(extractedImages);
        }

        // display current prediction as boxes in videostream
        this.renderPredictions(predictions);

        // continue in loop
        window.requestAnimationFrame(this.predictWebcam);
      });
    },
    clearState() {
      
      // passing empty array will force state cleanup
      predictionsSubject.next([]);

      // also remove detected images from canvas
      const current_sn_canvas = this.$refs.current_sn as any;
      const current_value_canvas = this.$refs.current_value as any;
      const current_tariff_canvas = this.$refs.current_tariff as any;

      const clearCanvas = (c: any) => c.getContext('2d').clearRect(0, 0, c.width, c.height);

      clearCanvas(current_sn_canvas);
      clearCanvas(current_value_canvas);
      clearCanvas(current_tariff_canvas);

      // clear form state
      this.form = {
        sn: "",
        value_t1: "",
        value_t2: "",
        value_p: "",
        value_smns: ""
      };

      // forget detected device type
      this.detectedDeviceType = null;
    },
    renderPredictions(predictions: Array<CocoSSD.DetectedObject>) {
      // clear predictions array
      this.predictions.splice(0, this.predictions.length);

      // add new predictions to list (to display them in UI)
      predictions.forEach(p => this.predictions.push(p));
    },
    takepicture() {
      const video = this.$refs.webcam as any;
      const canvas = this.$refs.canvas as any;

      const width = video.videoWidth;
      const height = video.videoHeight;

      const context = canvas.getContext("2d");
      canvas.width = width;
      canvas.height = height;
      context.drawImage(video, 0, 0, width, height);
      return cv.imread(canvas);
    }
  }
});
